import Swiper, { Navigation, Pagination } from 'swiper';
Swiper.use([Navigation, Pagination]);
const swiperHero = new Swiper('.hero-swiper', {
  loop: true,
  spaceBetween: 2,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  slidesPerView: 1,
  breakpoints: {
    320: {
      slidesPerView: 1,
      spaceBetween: 2
    },
    768: {
      slidesPerView: 2,
      spaceBetween: 2
    },
    // when window width is >= 1024px
    1023: {
      slidesPerView: 3,
      spaceBetween: 2
    },
  },
});

const swiperAlboms = new Swiper('.main-alboms-swiper', {
  loop: true,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  slidesPerView: 1,
  breakpoints: {
    // when window width is >= 1024px
    320: {
      slidesPerView: 1,
      spaceBetween: 2
    },
    768: {
      slidesPerView: 2,
      spaceBetween: 3
    },
    1439: {
      slidesPerView: 3,
      spaceBetween: 10
    },
  },
});


